.login {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 20;
  display: flex;
  justify-content: center;
  align-items: center;

  &[ishide="hide"] {
    display: none;
  }

  & .box {
    width: 300px;

    & .info {
      padding: 20px 0;

      & h2 {
        text-align: center;
      }

      & ul {
        padding-left: 45px;

        & li {
          list-style: disc;
          margin-bottom: 10px;
        }
      }
    }

    & button {
      width: 100%;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: $bgColor1;
      color: #fff;
      font-size: 24px;
      margin-top: 20px;
      opacity: 0.8;
      border-radius: 5px;

      &:hover {
        opacity: 1;
      }
    }
  }
}

.input_common {
  width: 100%;
  height: 70px;
  border-radius: 5px;
  border: 1px solid #ddd;
  padding-left: 20px;
  font-size: 20px;
  margin-top: 10px;
}

.wrapper {
  @include boxShadow();

  position: fixed;
  top: 5vh;
  left: 10%;
  width: 80%;
  height: 90vh;
  margin: 0 auto;
  background: #fff;
  overflow: hidden;
}

header {
  position: relative;
  color: $pointColor1;
  height: 70px;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eee;
  padding: 20px 10px 20px 20px;

  .userinfo {
    margin-left: 0;
    font-size: 27px;
    display: flex;
    justify-content: center;
    align-items: center;

    & .profile {
      width: 50px;
      height: 50px;
      border-radius: 5px;
      background: #243a65;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: 16px;
      margin-right: 10px;
      overflow: hidden;
      border: 1px solid #eee;

      & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .btn_menu {
    z-index: 11;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    flex-wrap: wrap;
    margin-right: 15px;

    & span {
      display: block;
      width: 100%;
      height: 4px;
      background: $pointColor1;
      padding: 0;
      position: relative;

      &:nth-child(2) {
        right: 0;
        opacity: 1;
      }
    }

    /* 메뉴버튼 눌렀을 때 */
    &[isshow="active"] span {
      &:nth-child(1) {
        transform: rotate(45deg);
        top: 10px;
      }
      &:nth-child(2) {
        right: -50px;
        opacity: 0;
      }
      &:nth-child(3) {
        transform: rotate(-45deg);
        top: -10px;
      }
    }
  }
}

.navigation {
  @include boxShadow();

  position: absolute;
  right: -100%;
  top: 0;
  height: 100%;
  width: 300px;
  background: #fff;
  z-index: 10;

  &[isshow="active"] {
    @include aniShowFromRight();
    right: 0;
  }

  & .head {
    height: 60px;
  }

  .buttonbox {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
    height: 60px;
    width: 100%;

    &:hover {
      background: $bgColor2;
    }

    & span {
      margin-left: 10px;
    }
  }
}

.Contents {
  position: relative;
  height: calc(100% - 70px);
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.listNavi {
  @include boxShadow();

  z-index: 1;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  line-height: 1;
  padding-top: 10px;
  background: #fff;

  & button.active svg {
    color: $pointColor2;
  }
}

/* 태블릿이하 사이즈 */
@media screen and (max-width: 991px) {
  .wrapper {
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    border-radius: 0;
    padding-bottom: 50px;
  }

  .listNavi {
    position: fixed;
    bottom: 0;
    left: 0;
  }
}

input {
  width: 100%;
}
