@charset "utf-8";

/* 리셋-에릭마이어 보완 */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
ol,
ul,
li,
dt,
dd {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input,
select {
  vertical-align: middle;
}
h1 {
  font-size: inherit;
  line-height: 1;
}
img {
  font-size: 0;
  line-height: 0;
  border: 0;
  vertical-align: middle;
}

html,
body {
  width: 100%;
  height: 100%;
}
body {
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 400;
  background: #f1f3f4;
}
body * {
  transition-duration: 0.2s;
}

html {
  font-family: sans-serif;
  /* 스마트폰 font-size 자동조정 해제, none으로하면 오류남 */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  /* 스마트폰 링크 터치시 하이라이트 없앰 */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  background-color: transparent;
  text-decoration: none;
  color: #666;
}
a:active,
a:hover {
  outline: 0;
  color: #000;
}

input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
select {
  text-transform: none;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button {
  border: 0;
  outline: 0;
  cursor: pointer;
  background: none;
}

