svg {
  font-size: 25px !important;
  color: #666;
}

svg:hover {
  color: $pointColor2;
}

.cat {
  @include catStyle();
}

/* left list section */
.list {
  border-right: 1px solid #eee;
  width: 320px;
  height: 100%;
  overflow-y: auto;
  position: relative;

  &[isoverflow="hidden"] {
    overflow: hidden;
  }

  & .list_title {
    font-size: 18px;
    font-weight: bold;
    padding: 20px 20px;
    color: $pointColor1;
    background: $bgColor3;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.roomlist {
  & ul {
    display: block;

    & li {
      display: block;

      &[isactive="active"] button {
        background: darken($bgColor2, 5%);
      }

      & button {
        position: relative;
        top: 0;
        background: #fff;
        padding: 20px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        text-align: left;
        flex-wrap: wrap;
        width: 100%;

        &:hover {
          top: -3px;
          background: $bgColor2;
        }

        & > * {
          display: inline-block;
          word-break: break-all;
          width: 100%;
        }

        & span {
          font-size: 14px;
          font-weight: 300;
          color: #999;
          margin-top: 5px;
        }

        & .left {
          width: 50px;
          height: 50px;
          border: 1px solid #eee;
          border-radius: 5px;
          display: flex;
          justify-content: center;
          align-items: center;
          overflow: hidden;

          & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        & .right {
          width: calc(100% - 70px);
          margin-left: 20px;
          line-height: 1;
        }
      }
    }
  }

  &.userList ul li:hover button {
    top: 0 !important;
    background: none !important;
    cursor: default;
  }
}

.icon_noneimg {
  font-size: 66px !important;
  color: #bbb;
}

/* right msg section */
.emptyCover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #fff;
  justify-content: center;
  align-items: center;
  z-index: 1;
  display: none;

  &[isshow="active"] {
    display: flex;
  }
}

.chat {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px;

  .chatWrap {
    position: relative;
    width: 100%;
    height: 100%;
    background: $bgColor3;
    padding: 0;
  }
}

.chatHeader {
  position: relative;
  height: 50px;
  background: #fff;
  padding: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  & .btn_closechat {
    position: absolute;
    left: 0;
    display: none;
  }

  & button {
    margin-left: 20px;
  }
}

.chatReciever {
  height: calc(100% - 100px);
  overflow-y: auto;
  padding: 10px 15px;
  text-align: center;
  position: relative;
  opacity: 0;
  transition-duration: 0s !important;

  &[isshow="active"] {
    opacity: 1;
    animation: slowShow 0.3s ease-in-out;
  }

  & * {
    transition-duration: 0s !important;
  }

  & img {
    max-width: 100%;
  }

  & .msg_reply {
    text-align: left;
    font-size: 12px;
    color: #888;
    padding: 5px;
    display: block;
  }

  & .icon_reply {
    position: relative;
    top: 5px;
    width: 20px;
  }

  & .msg_outer {
    position: relative;
    display: block;
    margin-bottom: 20px;

    &.active {
      @include animationFindMessage();
    }

    & .msg_inner {
      padding: 10px 15px;
      border-radius: 10px;
      position: relative;
      display: inline-block;
      max-width: 100%;
      min-height: 44px;
    }

    &.left {
      text-align: left;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;

      & .msg_left_profile {
        width: 50px;
        height: 50px;
        border-radius: 10px;
        border: 1px solid #eee;
        margin-right: 10px;
        overflow: hidden;

        & img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      & .msg_left_userinfo {
        width: calc(100% - 60px);
      }

      & .top_name {
        padding-bottom: 5px;
      }

      & .btm_msg {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
      }

      & .btm_msg::before {
        content: "";
        display: inline-block;
        width: 15px;
        height: 15px;
        position: absolute;
        top: 0;
        left: -2px;
        background: #fff;
        transform: skew(35deg, 0deg);
      }

      & .msg_inner {
        background: #fff;

        & .msg {
          word-break: break-all;
        }
      }
    }

    &.center .msg_inner {
      background: rgba(0, 0, 0, 0.03);
      font-size: 14px;
      border-radius: 50px;
    }

    &.right {
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;

      &::before {
        content: "";
        display: inline-block;
        width: 15px;
        height: 15px;
        position: absolute;
        top: 0;
        right: -2px;
        transform: skew(-35deg, 0deg);
        background: $bgColor4;
      }

      & .msg_inner {
        background: $bgColor4;
      }

      & .msg {
        text-align: left;
        word-break: break-all;
      }
    }

    & .msg button {
      color: blue;
      padding: 0 2px;
    }
  }
}

.chatSender {
  height: 50px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;

  & > button {
    line-height: 0;
  }

  & .file_sendfile,
  & .file_sendimage {
    position: fixed;
    left: -9999px;
  }

  & .input_inner {
    width: 100%;
  }

  & .input_send_msg {
    width: 100%;
    height: 40px;
    border: 0;
    outline: 0;
    background: #eee;
    border-radius: 50px;
    padding: 0 20px;
    box-sizing: border-box;
  }

  & .icon_send_msg {
    position: relative;
    color: $defaultColor1 !important;
  }
  & .icon_send_msg.active {
    color: $pointColor2 !important;
    right: -5px;
  }
}

/* 이모지 팝업 */
.popup_emoji {
  position: absolute;
  bottom: 70px;
  left: 20px;
  background: #fff;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 250px;
  height: 200px;
  overflow-y: auto;
  display: none;

  & ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    & li {
      margin: 5px;
      cursor: pointer;
    }
  }

  &[isshow="active"] {
    display: block;
  }
}

/* 팝업창 공용 */
.popup_right {
  @include boxShadow();

  position: absolute;
  right: -100%;
  top: 0;
  width: 300px;
  background: #fff;
  height: 100%;
  overflow-y: auto;

  &[isshow="active"] {
    @include aniShowFromRight();
    right: 0;
  }

  & button {
    position: relative;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;

    &:hover {
      background: $bgColor2;
    }
  }

  & .numbox {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 50px;
    text-align: right;
    padding-right: 20px;
    border-bottom: 1px solid #eee;

    & .num {
      color: $pointColor2;
    }
  }
}

/* 현재 채팅방 멤버 */
.popup_users {
  & .Chatuser_header button {
    justify-content: center;
    color: #2196f3;
  }

  & li {
    height: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;
    position: relative;

    & span {
      position: absolute;
      right: 20px;
      color: gray;
    }

    & .left {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: calc(100% - 80px);
    }
    & .left img {
      width: 50px;
      height: 50px;
      object-fit: cover;
      border: 1px solid #eee;
      border-radius: 5px;
      margin-right: 10px;
      margin-left: 8px;
    }
    & .right {
      position: absolute;
      right: 20px;
      top: 22px;
      color: gray;
    }
  }

  & p {
    text-align: center;
    padding: 15px;
  }
}

/* 멤버초대 */
.popup_invite {
  & > button {
    position: relative;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;

    &:hover {
      background: $bgColor2;
    }

    & .icon_check {
      position: absolute;
      right: 20px;
      top: 20px;
    }

    & img {
      width: 50px;
      height: 50px;
      border: 1px solid #eee;
      border-radius: 5px;
      margin-right: 10px;
      margin-left: 8px;
      object-fit: cover;
    }

    & .left {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    & .icon_noneimg {
      padding-left: 0;
    }
  }

  & .invite_header {
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;

    & button {
      display: block;
      width: 50%;
      height: 100%;

      &:hover {
        background: $bgColor2;
      }
    }

    & .btn_confirm {
      color: $pointColor2;
    }
  }
}

/* 방만들기 */
.createTopic {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  justify-content: center;
  align-items: center;
  text-align: center;
  display: none;
  opacity: 1;
  z-index: 2;

  &[isshow="active"] {
    display: flex;
  }

  & .input_wrap {
    & input {
      @include inputType1();
    }

    & p {
      position: relative;
      top: -40px;
      color: #999;
      z-index: 0;
      height: 40px;
    }

    & input:focus ~ p {
      top: -70px;
      font-size: 15px;
    }

    & input[valuecheck="active"] ~ p {
      opacity: 0;
    }
  }

  & label {
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;

    & input {
      width: 15px;
      height: 15px;
      margin-right: 5px;
      position: relative;
      top: 2px;
    }
  }

  & .btn_create {
    @include btnType1();
  }
}

.btn_create_mobile {
  display: none;
}

.btn_close_create {
  background: #ccc;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px auto 0;

  & svg {
    color: #fff;
    font-size: 30px !important;
  }

  &:hover {
    background: darken(#ccc, 10%);
  }
}

.btn_filedownload {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  position: relative;
  padding: 10px;

  & .filedown_text p {
    font-size: 11px;
    font-family: roboto;
  }

  & .icon_download {
    margin-left: 10px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.5);
    width: 40px;
    height: 40px;
    padding: 5px;
  }
}

.signup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
}

.signup_inner {
  max-width: 300px;
}

/* 태블릿이하 사이즈 */
@media screen and (max-width: 991px) {
  .list {
    width: 100%;
  }
  .chat {
    position: fixed;
    top: 100%;
    left: 0;
    padding: 0;

    &[isshow="active"] {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 12;
    }
  }

  .roomlist li[isactive="active"] button {
    background: none !important;
  }

  .chatHeader {
    line-height: 1;

    & .btn_closechat {
      display: block;
    }
  }
  .chatReciever {
    width: 100%;
  }

  .chatSender {
    padding-top: 0;
  }

  .btn_create_mobile {
    @include boxShadow();

    position: fixed;
    bottom: 120px;
    right: 10%;
    font-size: 50px;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #3f51b5;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);

    & svg {
      color: #fff;
      font-size: 30px !important;
    }
  }
}

.msg_imgbox {
  width: 200px;
  height: 200px;
  overflow: hidden;
  position: fixed;
  bottom: 100px;
}

.sf {
  position: fixed;
  width: 200px;
  height: 200px;
  background: green;
  top: 0;
  left: 0;

  & a {
    display: inline-block;
    width: 50px;
    height: 50px;
    background: #fff;
  }
}

.signup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;

  & h3 {
    text-align: center;
    font-size: 1.5em;
  }
}

.btn_signup {
  display: block;
  text-align: center;
  background: gray;
  width: 100%;
  height: 70px;
  color: #fff;
  margin-top: 10px;
  border-radius: 5px;

  &:hover {
    background: lighten(gray, 10%);
  }
}

.btn_login {
  text-align: center;
  margin-top: 50px;
  width: 100%;
  height: 50px;
  padding: 10px;
  background: $bgColor1;
  color: #fff;
  border-radius: 5px;
}
.btn_login:hover {
  background: lighten($bgColor1, 10%);
}

.add_propfile {
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px auto 0;
  background: none;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #ddd;
  position: relative;
  cursor: pointer;
}

.add_propfile img {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.add_propfile input {
  position: fixed;
  left: -9999px;
}

.icon_profile {
  font-size: 40px !important;
}

.text_profile {
  text-align: center;
  margin: 10px auto 40px;
  color: gray;
}

/* 채팅방 내 @ 입력 시 뜨는 회원찾기목록 component */
.searchUsers {
  position: absolute;
  left: 0;
  bottom: 50px;
  height: 300px;
  width: 100%;
  overflow: hidden;
  background: #fff;
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.05);

  & h3 {
    padding: 10px;
    font-size: 14px;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  & ul {
    overflow: auto;
    overflow-x: hidden;
    height: calc(100% - 40px);
    padding: 5px 0;
  }

  & li {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & button {
      position: relative;
      width: 100%;
      text-align: left;
      padding: 5px;

      &:hover {
        background: #f1f3f4;
        top: -1px;
      }

      & img {
        width: 50px;
        height: 50px;
        object-fit: cover;
        margin-right: 10px;
        border-radius: 5px;
        border: 1px solid #eee;
      }
    }
  }
}

.popupMessage {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  & .con {
    z-index: 1;
    background: #fff;
    border-radius: 10px;
    width: 90%;
    max-width: 600px;
    text-align: center;
    overflow: hidden;

    & button {
      display: block;
      justify-content: center;
      align-items: center;
      width: 100%;
      padding: 10px 0;
      border-bottom: 1px solid #eee;
      font-size: 17px;

      &:hover {
        background: #f1f3f4;
      }

      &:last-child {
        border-bottom: 0;
      }
    }
  }
  & .bg {
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
  }
}

.input_msg_copy {
  position: fixed;
  left: -9999px;
}

/* 답장 메시지 */
.ItemReply {
  position: absolute;
  bottom: 50px;
  left: 0;
  min-height: 60px;
  width: 100%;
  background-color: #f5f5f5;

  & .top {
    background: #fff;
    padding: 5px 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & .icon_reply {
      margin-right: 5px;
    }

    & span {
      font-size: 14px;
    }
  }

  & .btm {
    padding: 5px 10px;

    & img {
      height: 40px;
    }

    & span {
      word-break: break-all;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      padding: 5px 10px;
      color: #999;
    }
  }
}

.ItemRelay {
  position: absolute;
  bottom: 0;
  left: 0;
  min-height: 60px;
  width: 100%;
  background-color: #fff;
  border-top: 1px solid #000;

  & .top {
    font-size: 17px;
    font-weight: 500;
    padding: 10px;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
  }

  & .mid {
    padding: 0;
    width: 100%;

    & .tab {
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 10px 10px rgba(0, 0, 0, 0.03);

      & button {
        display: block;
        text-align: left;
        width: 100%;
        padding: 10px;
        border-bottom: 3px solid transparent;
        font-size: 17px;
        color: #999;

        &.active,
        &:hover {
          border-bottom: 1px solid #2196f3;
          color: #000;
          background: #f4f7f9;
        }
      }
    }

    & .relaylist {
      padding: 15px 0px 0;
      overflow: hidden;

      & ul {
        display: block;
        overflow-x: auto;
        white-space: nowrap;
        padding: 10px 10px 0;

        & li {
          text-align: center;
          width: 70px;
          height: 70px;
          display: inline-block;
          margin-right: 20px;
          margin-bottom: 40px;
          vertical-align: top;
        }
        & li label {
          display: block;
          position: relative;
          height: 100%;
          cursor: pointer;
        }
        & li label img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        & li label .name {
          text-overflow: ellipsis;
          overflow: hidden;
        }

        & li label input {
          position: fixed;
          left: -9999px;
        }

        & .icon_unchecked {
          display: block;
        }
        & .icon_checked {
          display: none;
        }

        & .checkicon {
          position: absolute;
          right: -10px;
          bottom: -10px;
          background: #fff;
        }

        & li label input:checked ~ .icon_unchecked {
          display: none;
        }
        & li label input:checked ~ .icon_checked {
          display: block;
          color: #2196f3;
        }
      }
    }
  }

  & .btm {
    display: flex;
    justify-content: space-between;
    align-content: center;
    height: 50px;

    & button {
      display: block;
      background-color: #f5f5f5;
      width: 100%;
      width: 49%;

      &:hover {
        background: darken(#f5f5f5, 10%);
      }
    }

    & .btn_submit {
      background: $pointColor2;
      color: #fff;

      &:hover {
        background: darken($pointColor2, 10%);
      }
    }
  }
}

.BtnRefresh {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 50%;

  &:hover {
    transform: rotate(360deg);
    background: $pointColor2;

    & svg {
      color: #fff;
    }
  }
}

.icon_room {
  background: $pointColor2;
  color: #fff;
  font-size: 70px !important;
  padding: 10px;
}
.icon_room:hover {
  color: #fff !important;
}
