$fontColorDefault: #fff;
$pointColor1: #243a65;
$pointColor2: #2196f3;
$pointColor3: #ff7aa0;
$bgColor1: #2196f3;
$bgColor2: #f1f3f4;
$bgColor3: #f4f7f9;
$bgColor4: #b5eeff;
$hoverColor: #f5f7f9;
$defaultColor1: gray;

@mixin boxShadow {
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
}

@mixin aniShowFromRight {
  animation: ani_show_invite 0.3s ease-out;
  @keyframes ani_show_invite {
    0% {
      right: -100%;
    }
    100% {
      right: 0;
    }
  }
}

@mixin btnType1 {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $bgColor1;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  font-size: 16px;

  &:hover {
    background: darken($bgColor1, 10%);
  }
}

@mixin inputType1 {
  position: relative;
  width: 100%;
  height: 50px;
  text-align: center;
  font-size: 16px;
  border: 0;
  outline: 0;
  border-bottom: 1px solid #ddd;
  z-index: 1;
  background: none;

  &:focus {
    border-bottom: 1px solid #000;
  }
}

@mixin animationFindMessage {
  @keyframes findMessage {
    0% {
      position: relative;
      top: -20px;
    }
    20% {
      top: 0px;
    }
    40% {
      top: -20px;
    }
    60% {
      top: 0px;
    }
    80% {
      top: -20px;
    }
    100% {
      top: 0;
    }
  }
  animation: findMessage 1s ease-in-out;
}

