.Notify {
  @include boxShadow();
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
  height: 100vh;
  padding: 15px 10px;
  z-index: 100;
  display: inline-block;
  background: #fff;

  & > div > p {
    padding: 10px;
    text-align: center;
    font-size: 16px;
    color: gray;
    font-weight: 300;
  }

  & h2 {
    position: relative;

    & .icon_close {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 30px !important;
      cursor: pointer;
    }
  }

  & h3 {
    margin-top: 20px;
    border-bottom: 1px solid #333;
    padding-bottom: 10px;
    font-size: 17px;
  }

  & .notify_reply {
    & h3 {
      border-bottom: 1px solid $pointColor2;
    }
  }
  & .notify_mention {
    & h3 {
      border-bottom: 1px solid $pointColor3;
    }
  }

  & .msgbox {
    width: 100%;
    height: 65px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    position: relative;
    padding: 10px 0;
    margin: 0;
    border-bottom: 1px solid #eee;
    cursor: pointer;

    &:hover {
      background: $hoverColor;
    }

    & .left {
      width: 40px;
      font-size: 12px;
      text-align: center;
      color: #666;

      &[msgtype="mention"] * {
        color: $pointColor3;
      }
      &[msgtype="reply"] * {
        color: $pointColor2;
      }

      & p {
        line-height: 0.6;
      }
    }

    & .right {
      text-align: left;
      width: 210px;
      font-size: 14px;
      padding-left: 5px;
      white-space: nowrap;
      & p {
        display: block;
        overflow: hidden;
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 12px;
        color: #333;
      }
      & span {
        display: inline-block;
        overflow: hidden;
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #333;
        font-size: 14px;
        padding-top: 3px;
      }
    }

    & .icon_close {
      width: 20px;
      cursor: pointer;
    }
  }
}

.btnViewNotify {
  @include boxShadow();

  position: fixed;
  width: 80px;
  height: 80px;
  right: 0;
  background: #fff;
  top: calc(50% - 40px);
  display: flex;
  justify-content: center;
  align-items: center;

  & .bell_newmsg {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 12px;
    font-family: roboto;
    top: -20px;
    left: 0;

    & .dot {
      display: inline-block;
      width: 5px;
      height: 5px;
      background: red;
      position: relative;
      top: -7px;
      left: -2px;
    }
  }
}
